import {
  customElement,
  html
} from "../_snowpack/pkg/functional-element.js";
import {html as htmlLit} from "../_snowpack/pkg/lit-html.js";
import {StorePromise} from "../state/store.js";
import "./pc-router.js";
import "./pc-main-menu.js";
import "./pc-player.js";
import {
  normalShadow,
  one,
  two,
  pxSmall,
  pxLarge,
  pxXLarge,
  color1Full,
  color2Full,
  alertPadding
} from "../services/css.js";
import "../services/listeners.js";
import {pcAlert} from "./pc-modal.js";
import {createWallet} from "../services/balance-calculations.js";
import "../services/listeners.js";
import "./pc-loading.js";
StorePromise.then((Store) => {
  customElement("pc-app", ({constructing, connecting, update, loaded}) => {
    if (constructing) {
      Store.subscribe(update);
      if (Store.getState().walletCreationState === "NOT_CREATED") {
        createWallet(Store);
      }
      return {
        loaded: false
      };
    }
    if (connecting) {
      setTimeout(() => {
        update({
          loaded: true
        });
      });
    }
    const state = Store.getState();
    const currentEpisode = state.episodes[state.currentEpisodeGuid] || "NOT_FOUND";
    return html`
            <style>
                html {
                    height: 100%;
                    font-family: sans-serif;
                }

                body {
                    height: 100%;
                    margin: 0;
                    background-color: white;
                }

                .pc-app-container {
                    width: ${state.screenType === "DESKTOP" ? "50vw" : "100vw"};
                    margin-left: ${state.screenType === "DESKTOP" ? "auto" : "0px"};
                    margin-right: ${state.screenType === "DESKTOP" ? "auto" : "0px"};
                    box-shadow: ${state.screenType === "DESKTOP" ? normalShadow : "none"};
                    height: 100%;
                    box-sizing: border-box;
                }

                .pc-app-top-bar {
                    position: fixed;
                    box-sizing: border-box;
                    display: flex;
                    align-items: center;
                    width: ${state.screenType === "DESKTOP" ? "50vw" : "100vw"};
                    box-shadow: ${normalShadow};
                    z-index: ${two};
                    background-color: white;
                }

                .pc-app-menu-icon {
                    font-size: ${pxLarge};
                    cursor: pointer;
                    background-color: white;
                    z-index: ${one};
                    padding-top: ${pxSmall};
                    padding-left: ${pxSmall};
                    padding-bottom: ${pxSmall};
                }

                .pc-app-payout-problem-icon-container {
                    padding-right: ${pxSmall};
                    z-index: ${one};
                    background-color: white;
                }

                .pc-app-payout-problem-icon {
                    color: red;
                    font-size: ${pxXLarge};
                    cursor: pointer;
                }

                .pc-app-scrolling-title {
                    white-space: nowrap;
                    font-weight: bold;
                    animation: marquee 30s linear infinite;
                }

                @keyframes marquee {
                    0% {
                        transform: translateX(100%);
                    }

                    100% {
                        transform: translateX(-100%);
                    }
                }

                a:link {
                    color: ${color2Full};
                }

                a:visited {
                    color: ${color1Full}
                }

            </style>

            <div class="pc-app-container">
                <pc-loading
                    .prename=${"pc-app-"}
                    .hidden=${loaded}
                ></pc-loading>

                <div class="pc-app-top-bar">

                    <i 
                        class="material-icons pc-app-menu-icon"
                        @click=${mainMenuToggle}
                    >
                        menu
                    </i>

                    ${currentEpisode !== "NOT_FOUND" ? html`
                        <div style="overflow: hidden; width: 100%">
                            <div class="pc-app-scrolling-title">${currentEpisode.title}</div>
                        </div>
                    ` : html`
                        <div style="color: grey; font-weight: bold; margin-left: auto; margin-right: calc(10px + 1vmin); font-size: calc(15px + 1vmin)">Podcrypt Beta</div>
                    `}

                    <!-- <div
                        ?hidden=${Store.getState().payoutProblem === "NO_PROBLEM"}
                        class="pc-app-payout-problem-icon-container"
                    >
                        <i 
                            class="material-icons pc-app-payout-problem-icon"
                            @click=${() => {
      pcAlert(getPayoutProblemMessage(Store.getState().payoutProblem), state.screenType);
    }}
                        >
                            error_outline
                        </i>  
                    </div> -->
                </div>
        
                <pc-main-menu></pc-main-menu>
                <pc-router></pc-router>
                <pc-player></pc-player>
            </div>
    
        `;
  });
  function mainMenuToggle(e) {
    e.stopPropagation();
    Store.dispatch({
      type: "TOGGLE_SHOW_MAIN_MENU"
    });
  }
  function getPayoutProblemMessage(payoutProblem) {
    if (payoutProblem === "BALANCE_0") {
      return htmlLit`
                <div style="${alertPadding}">
                    <div>There is a problem with your next payout:</div>
                    <br>
                    <div>You have a balance of 0</div>
                </div>
            `;
    }
    if (payoutProblem === "PAYOUT_TARGET_0") {
      return htmlLit`
                <div style="${alertPadding}">
                    <div>There is a problem with your next payout:</div>
                    <br>
                    <div>Your payout target is $0</div>
                </div>
            `;
    }
    if (payoutProblem === "BALANCE_LESS_THAN_PAYOUT_TARGET") {
      return htmlLit`
                <div style="${alertPadding}">
                    <div>There is a problem with your next payout:</div>
                    <br>
                    <div>Your balance is less than your payout target</div>
                </div>
            `;
    }
    return htmlLit`<div style="${alertPadding}">There is no problem with your next payout</div>`;
  }
});
